:root
{
    --burger-menu-background: rgba(73, 86, 92, 0.8);
    --burger-menu-button-text: white;
    --burger-menu-item-text: white;
}

.burger-menu
{
    position: fixed;
    top: 0.5em;
    left: 0.5em;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    gap: 0.25em;
    opacity: 0.5;
    transition: opacity 0.25s;
    color: var(--burger-menu-button-text);
}

.burger-menu:hover
{
    opacity: 1;
}

.burger-menu > .button
{
    background: var(--burger-menu-background);
    width: 2em;
    height: 2em;
    border-radius: 0.5em;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
}

.burger-menu.expanded > .items
{
    display: block;
    z-index: 5;
}

.burger-menu > .items
{
    min-width: 6em;
    border-radius: 0.25em;
    display: none;
    overflow: hidden;
    animation: slide-in 0.25s ease-out;
}

.burger-menu > .items > .item
{
    padding: 0.25em;
    cursor: pointer;
    background: var(--burger-menu-background);
    color: var(--burger-menu-item-text);
    font-size: 0.8em;
    font-weight: bold;
    opacity: 0.8;
    transition: opacity 0.25s;
}

.burger-menu > .items > .item.active,
.burger-menu > .items > .item:hover
{
    opacity: 1;
}

.burger-menu > .items > .item.active::before
{
    content: '> ';
}

@keyframes slide-in
{
    from {
        transform: translateY(-2em);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}